@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.ac-intro-summary {
    line-height: 1.4rem;
    font-size: 1rem;
}

.ac-intro-heading {
    font-weight: 500;
    font-size: 3.5rem;
    line-height: 1.2;
}


/*Phone sizes portrait*/
@media (min-width: 575px) {
    .ac-intro-summary {
        line-height: 1.4rem;
        font-size: 1rem;
    }

    .ac-intro-heading {
        font-weight: 500;
        font-size: 3.6rem;
        line-height: 1.2;
    }

}

/*Phone sizes landscape*/
@media (min-width: 768px) {
    .ac-intro-summary {
        line-height: 1.4rem;
        font-size: 1rem;
    }
    .ac-intro-heading {
        font-weight: 500;
        font-size: 3.8rem;
        line-height: 1.2;
    }
}

/*Tablet sizes*/
@media (min-width: 992px) {
    .ac-intro-heading {
        font-weight: 500;
        font-size: 3rem;
        line-height: 1.2;
    }

    .ac-intro-summary {
        font-size: 0.9rem;
        line-height: 1.8rem;
    }

}

/*Computer sizes*/
@media (min-width: 1200px) {
    .ac-intro-heading {
        font-weight: 500;
        font-size: 4.4rem;
        line-height: 1.2;
    }

    .ac-intro-summary {
        font-size: 1rem;
        line-height: 1.8rem;
    }
}
